import styles from "./styles.module.css"
import Footer from "../../components/Footer";
import Layout from "../../components/Layout";
import StyledWindow from "../../components/StyledWindow";
import { useTitle } from "../../utils/useTtile";
import useStateObj from "../../utils/useStateObj";
import Textbox from "../../components/Textbox";
import classNames from "../../utils/classNames";
import Title from "../../components/Title";

export default function TimetablePage()
{
	useTitle("");
	const timetable = useStateObj(0);
	const items = [
		data_timetable,
		data_autograph,
		data_lections,
		data_tournaments,
	][timetable.v];

	return (
		<Layout centeredPage className={styles.root} footer={<Footer curPage="timetable" />}>
			<Title className={styles.title} />
			<div className={styles.body}>
				<StyledWindow className={styles.list} scrollUpdate={timetable.v}>
					<div className={styles.items}>
						{items.map((el, i) => el.title ?
							<div className={styles.item_title} key={i}>{el.text}</div>
							:
							<Textbox small dark key={i}><div className={styles.item}>{el.text}</div></Textbox>
						)}
					</div>
				</StyledWindow>
				<div className={styles.btns}>
					<Textbox small btn highlight={timetable.v == 0}>
						<button onClick={() => timetable.set(0)} className={classNames(styles.btn, "title")}></button>
					</Textbox>
					{/* <Textbox small btn highlight={timetable.v == 1}>
						<button onClick={() => timetable.set(1)} className={classNames(styles.btn, "title")}></button>
					</Textbox>
					<Textbox small btn highlight={timetable.v == 2}>
						<button onClick={() => timetable.set(2)} className={classNames(styles.btn, "title")}></button>
					</Textbox> */}
					<Textbox small btn highlight={timetable.v == 3}>
						<button onClick={() => timetable.set(3)} className={classNames(styles.btn, "title")}></button>
					</Textbox>
				</div>
			</div>
		</Layout>
	);
}

